.card-container {
    background-color: #fff;
    width: 420px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

  }
  
  .card-element {
    margin-bottom: 30px;
  
  }
  
  .card-element label {
    font-weight: bold;
    margin-bottom: 5px;
    display: block;
  }
  
  .card-element input[type="text"] {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    outline: none;
  }
  
  .card-element input[type="text"]:focus {
    border-color: #007bff;
  }

  .flex{
    display: flex;
    justify-content: space-between;

  }
  .card-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 52px; /* Adjust the gap between cards */
}

.card{
    width:250px;
    padding: 50px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
  
}

.summary-div{
  /* background-color:#6c757d; */
  /* width: 100px; */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
}

